import React, { useEffect, useRef, useState } from 'react';
import { StaticImage } from 'gatsby-plugin-image';
import styled from 'styled-components';
import { srConfig } from '@config';
import sr from '@utils/sr';
import { usePrefersReducedMotion } from '@hooks';
import AudioButton from '@components/audioBution';

const StyledAboutSection = styled.section`
  max-width: 900px;

  .inner {
    display: grid;
    grid-template-columns: 3fr 2fr;
    grid-gap: 50px;

    @media (max-width: 768px) {
      display: block;
    }
  }
`;

const StyledText = styled.div`
  ul.skills-list {
    display: grid;
    grid-template-columns: repeat(2, minmax(140px, 200px));
    grid-gap: 0 10px;
    padding: 0;
    margin: 20px 0 0 0;
    overflow: hidden;
    list-style: none;

    li {
      position: relative;
      margin-bottom: 10px;
      padding-left: 20px;
      font-family: var(--font-mono);
      font-size: var(--fz-xs);

      &:before {
        content: '▹';
        position: absolute;
        left: 0;
        color: var(--green);
        font-size: var(--fz-sm);
        line-height: 12px;
      }
    }
  }
`;

const StyledPic = styled.div`
  position: relative;
  max-width: 300px;
  text-align: center; /* Center-aligns the text below the image */

  @media (max-width: 768px) {
    margin: 50px auto 0;
    width: 70%;
  }

  .wrapper {
    ${({ theme }) => theme.mixins.boxShadow};
    display: block;
    position: relative;
    width: 100%;
    border-radius: var(--border-radius);
    background-color: var(--green);
    cursor: pointer;

    &:hover,
    &:focus {
      outline: 0;
      transform: translate(-4px, -4px); /* Adds hover effect with movement */

      &:after {
        transform: translate(8px, 8px);
      }

      .img {
        filter: none;
        mix-blend-mode: normal;
      }
    }

    .img {
      position: relative;
      border-radius: var(--border-radius);
      mix-blend-mode: multiply;
      filter: grayscale(100%) contrast(1); /* Grayscale filter for hover effect */
      transition: var(--transition);
    }

    &:before,
    &:after {
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      border-radius: var(--border-radius);
      transition: var(--transition);
    }

    &:before {
      top: 0;
      left: 0;
      background-color: var(--navy);
      mix-blend-mode: screen;
    }

    &:after {
      border: 2px solid var(--green);
      top: 14px;
      left: 14px;
      z-index: -1;
    }
  }

  .caption {
    margin-top: 20px;
    font-size: var(--fz-sm); /* Font size for the caption */
    color: var(--light-slate); /* Caption text color */
  }
`;

const StyledModal = styled.div`
  display: ${({ show }) => (show ? 'flex' : 'none')}; /* Shows modal only when "show" is true */
  justify-content: center; /* Centers modal horizontally */
  align-items: center; /* Centers modal vertically */
  position: absolute; 
  top: 50px;
  left: -280px;
  width: 100vw; /* Full-screen width */
  height: 100vh; /* Full-screen height */
  background-color: rgba(0, 0, 0, 0); /* Semi-transparent black background */
  z-index: 1000; /* Ensures modal is on top of other content */

  .video-container {
    position: relative; /* Ensures positioning of child elements */
    width: 80%; /* Video container width */
    max-width: 985px; /* Maximum width for larger screens */
    background: #000; /* Black background for video container */
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3); /* Adds a shadow effect for the modal */
    border-radius: 8px; /* Adds rounded corners to the container */

    video {
      width: 100%; /* Ensures video fills the container width */
      height: auto; /* Maintains video aspect ratio */
      border-radius: 8px; /* Matches the container's rounded corners */
    }

    .close-button {
      position: absolute; /* Positions close button relative to container */
      top: 10px; /* Distance from top of the container */
      right: 10px; /* Distance from right of the container */
      background: none; /* Removes default button background */
      border: none; /* Removes border */
      color: var(--lightest-slate);
      font-size: 32px; /* Larger font size for visibility */
      cursor: pointer; /* Pointer cursor for interactivity */
      z-index: 1100;

      &:hover {
        color: var(--green); /* Changes color on hover */
      }
    }
  }
`;


const About = () => {
  const revealContainer = useRef(null);
  const videoRef = useRef(null); // Ref to access the video element
  const prefersReducedMotion = usePrefersReducedMotion();
  const [showModal, setShowModal] = useState(false);

  useEffect(() => {
    if (prefersReducedMotion) {
      return;
    }

    sr.reveal(revealContainer.current, srConfig());
  }, []);

  const skills = ['React', 'Three.js', 'Javascript', 'HTML/CSS', 'Node.js', 'C/C++'];

  // Close modal and stop video
  const closeModal = () => {
    setShowModal(false); // Hide the modal
    if (videoRef.current) {
      videoRef.current.pause(); // Pause the video
      videoRef.current.currentTime = 0; // Reset playback position
    }
  };

  return (
    <StyledAboutSection id="about" ref={revealContainer}>
      <h2 className="numbered-heading">About Me</h2>

      <div className="inner">
        <StyledText>
          <div>
            <p>
              Hello! My name is Yuhan Li. For the first two years of my college education, I was
              studying at Sichuan University in China, but later I transferred to Rutgers University
              for better academic and career opportunities in the U.S. Until now, I've explored a wide
              range of subjects in computer science, including machine learning, computer systems,
              computer graphics, game development, and cryptography.
            </p>
            <p>
              Fast-forward to today, I find myself interested in computer graphics, but due to the very
              few career opportunities left for beginners in this field, I decide instead to start my
              career as a frontend developer, which I believe is a more practical and promising field.
            </p>
            <p>
              In my spare time, I enjoy reading books, <AudioButton />, appreciating art, and
              kickboxing.
            </p>
            <p>Here are a few technical skills I’ve been working with recently:</p>
          </div>
          <ul className="skills-list">
            {skills && skills.map((skill, i) => <li key={i}>{skill}</li>)}
          </ul>
        </StyledText>

        <StyledPic>
          <div className="wrapper" onClick={() => setShowModal(true)}>
            <StaticImage
              className="img"
              src="../../images/me.jpg"
              width={500}
              quality={95}
              formats={['AUTO', 'WEBP', 'AVIF']}
              alt="Headshot"
            />
          </div>
          <p className="caption">click above to hear the soundtrack of my life</p>
        </StyledPic>
      </div>

      <StyledModal show={showModal}>
        <div className="video-container">
          <button className="close-button" onClick={closeModal}>
            ×
          </button>
          <video ref={videoRef} controls>
            <source src="/videos/Soundtrack.mp4" type="video/mp4" />
            Your browser does not support the video tag.
          </video>
        </div>
      </StyledModal>
    </StyledAboutSection>
  );
};

export default About;